<template>
  <h1 class="titre">Anne de Bretagne</h1>
  <p class="descriptif">Anne de Bretagne, née le 25 janvier 1477 à Nantes et morte le 9 janvier 1514 à Blois, fille du duc de Bretagne François II, devient duchesse de Bretagne à la mort de son père, puis reine de France une première fois en 1491 après son mariage avec le roi de France Charles VIII, et une deuxième fois en 1499 après un second mariage avec le roi Louis XII.</p>
  <MapComponent />


  <div class="container">
    <div class="item" v-for="(image, index) in images" :key="index">
      <img :src="image.src" :alt="image.alt">
      <!-- rect: Rectangle -->
      <div class="shape rect rectangle-32bfb2516c1d">
        <div class="shape circle ellipse-haut-droite"></div>
        <div class="shape circle ellipse-haut-gauche"></div>
        <div class="shape circle ellipse-bas-droite"></div>
        <div class="shape circle ellipse-bas-gauche"></div>

        <div class="texte" v-html="image.text"></div>
      </div>
    </div>

  </div>



</template>

<script>
import MarkdownIt from 'markdown-it';
import MapComponent from '../MapComponent.vue';
import '@/assets/css/duchesses.css';

export default {
  name: 'AnneDeBretagne',
  components: {
    MapComponent
  },

  data() {
    return {
      images: []
    };
  },
  async created() {
    const context = require.context('@/assets/images/AnneDeBretagne', false, /\.(jpeg|jpg|png)$/);
    this.images = context.keys().map((key, index) => ({
      id: index + 1,
      src: context(key),
      title: `Image ${index + 1}`,
      text: ''
    }));
  },
  mounted() {
    this.loadMarkdownTexts();
  },
  methods: {
    async loadMarkdownTexts() {
      const md = new MarkdownIt();
      for (let image of this.images) {
        try {
          // Charger le fichier Markdown correspondant à l'image
          const response = await import(`@/assets/markdown/AnneDeBretagne/text${image.id}.md`);
          image.text = md.render(response.default);
        } catch (error) {
          console.error(`Erreur lors du chargement du fichier Markdown pour l'image ${image.id}:`, error);
        }
      }
    }
    ,
  },
}
</script>